/* src/Dashboard.css */
body {
  margin: 0;
  font-family: Poppins, sans-serif;
}

.dashboard {
  text-align: center;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #2a2e43;
  padding: 10px 20px;
  color: white;
}

.logo {
  font-size: 1.5em;
  font-weight: bold;
}

.nav-links {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 330px;
  padding: 10px;
  top: 0;
  left: 330px;
  position: fixed;
  width: 54%;
}

.nav-links a {
  color: white;
  text-decoration: none;
}

.hamburger {
  display: none;
  font-size: 1.5em;
  cursor: pointer;
}

.header {
  background-color: #2a2e43;
  color: white;
  padding: 20px;
}

.restaurant-info {
  display: flex;
  align-items: center;
  justify-content: center;
}

.restaurant-info img {
  border-radius: 50%;
  margin-right: 20px;
}

.content {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 20px;
  /* border: solid 10px black; */
  /* border-radius: 50px; */

  /*   
  border-radius: 30px 30px 0 0;
  overflow: hidden;
  background-color: white;
  box-shadow: 0px 4px 100px 0px rgba(0, 0, 0, 0.25);
  padding: 2rem; */
}

.card {
  background-color: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  width: 315px;
  height: 230px;
  margin: 16px;
  padding: 34px;
  text-align: center;
  transition: transform 0.2s;
  cursor: pointer;
}

.card:hover {
  transform: scale(1.05);
}

.card img {
  width: 50px;
  height: 50px;
  margin-bottom: 10px;
}

footer {
  margin: 20px 0;
  font-size: 0.9em;
  color: #888;
}

/* Responsive styles */
@media (max-width: 768px) {
  .nav-links {
    display: none;
    flex-direction: column;
    width: 100%;
    position: absolute;
    top: 60px;
    left: 0;
    background-color: #2a2e43;
  }

  .nav-links.open {
    display: flex;
  }

  .hamburger {
    display: block;
  }

  .logo {
    flex: 1;
  }

  .navbar {
    position: relative;
  }

  /* .content {
      flex-direction: column;
    } */
}
