.OrdersContent {
  width: 100%;
}
.catagoreisOrders {
  margin-top: 20px;
  padding: 20px;
}
.RequestInprogress {
  display: flex;
  padding: 10px 50px;
  gap: 50px;
}
.SelectTitleProgress {
  color: #1e1e1e;
  cursor: pointer;
}
.RequestInprogress .active {
  color: #f8951d;
}
.RequestInprogress .active::after {
  content: "";
  display: block;
  width: 200%;
  height: 4px;
  text-align: center;
  background-color: #f8951d;
  margin: 0 auto;
  position: relative;
  top: 33px;
  left: 50%;
  transform: translateX(-50%);
  /* This will center the line horizontally */
}
.horLine {
  border-bottom: 1px solid #797979;
  margin: 0 20px;
}
