@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@400;600;700&display=swap');
.forgot-password-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    font-family: 'Nunito Sans', sans-serif;
}

.forgot-password-container {
    width: 500px;
    height: auto;
    padding: 20px;
    background-color: #fff;
    border-radius: 20px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
    border: 10px solid #FFFFFF80;
    text-align: start;
}
.close-modal {
    position: relative;
    top: -140px;
    left: 550px;
    font-size: 20px;
    cursor: pointer;
    color: #333;
    background-color: #00000059;
    height: 30px;
    width: 30px;
    border-radius: 100px;
    cursor: pointer;
    z-index: 2000;
    border: none;
  }

.logo img {
    width: 132px;
    margin-bottom: 8px;
}

.forgot-password-container h2 {
    font-size: 32px;
    font-weight: 700;
    color: #000000;
    margin-top: 8px;
    margin-bottom: 0;
    line-height: 38.4px;
}
.forgot-password-container span{
    font-size: 14px;
    color: #F8951D;
    line-height: 19.1px;
}

.forgot-password-container p {
    font-size: 15px;
    color: #999EA1;
    font-weight: 400;
    line-height: 21.6px;
    margin-top: 8px;
    margin-bottom: 5px;
}

.forgot-password-container input[type="email"] {
    width: 100%;
    height: 43px;
    padding: 12px;
    font-size: 16px;
    border: 1px solid #C6C6C6;
    border-radius: 10px;
    margin-top: 10px;
    box-sizing: border-box;
}

.continue-button {
    width: 100%;
    height: 47px;
    background-color: #F8951D;
    color: white;
    font-size: 14px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    margin-top: 20px;
}


