.Dashboard {
  display: flex;
  flex-direction: row;
  height: 130vh;
  max-height: 180vh;
  background-color: #f5f7fa;
  padding: 0 20px;
}
.DashboardContent {
  width: 100%;
}
.MenuContent {
  width: 100%;
}
.headersOrderFilter {
  display: flex;
  margin: 10px 0;
}
.allorderHeader {
  margin: 0;
  text-align: left;
  width: max-content;
  padding: 0 40px;
  color: #343c6a;
}
.orderFilterButton {
  border: none;
  appearance: none;
  color: #718ebf;
  background-color: transparent;
}
.orderFilter {
  display: flex;
  align-items: center;
  border-radius: 10px;
  background-color: white;
  width: max-content;
  padding: 5px;
}
.ordersfiltercontent {
  width: 60%;
  display: flex;
  align-items: center;
}
.InprogresscontentHeading {
  color: #343c6a;
  padding: 0 20px;
}
