.signup-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.signup-box {
  padding: 40px 60px;
  border-radius: 10px;
  text-align: center;
  max-width: 400px;
  width: 400px;
  max-height: 570px;
}

.logo {
  width: 132px;
  height: 50px;
}

.signup-box h1 {
  color: black;
  font-size: 25px;
  margin: 20px 0;
  text-align: left;
}

.welcome-text {
  font-size: 14px;
  text-align: left;
  color: #999ea1;
  margin-bottom: 20px;
}

.signup-box .input-group {
  margin-bottom: 20px;
  text-align: left;
}

.signup-box .input-group label {
  display: block;
  margin-bottom: 5px;
  color: #f8951d;
}

.signup-box .input-group input {
  width: -webkit-fill-available;
  padding: 10px;
  border: 1px solid #c6c6c6;
  border-radius: 10px;
  font-size: 1rem;
}

.password-container {
  position: relative;
}

.toggle-password {
  display: flex;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  cursor: pointer;
  color: #888;
}
.toggle-phoneNumber{
 margin-top: 5px;
 font-size: 1rem;
}

.signup-button {
  background-color: #f8951d;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 10px;
  font-size: 1rem;
  cursor: pointer;
  width: 100%;
}

.login-link {
  margin-top: 20px;
  color: #333;
}

.login-link a {
  color: #ff8800;
  text-decoration: none;
}
.phone-input-container{
  display: flex;
  align-items: center;
  border: 1px solid #c6c6c6;
  border-radius: 10px;
  padding: 0 .4rem;
}
.phone-input-container select{
  border: none;
  outline: none;
}
.error-message {
  color: #eb5757;
  font-size: 12px;
  margin-top: 5px;
}