/* Status.css */
.status {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.toggle-status {
  display: flex;
  align-items: center;
  width: calc(100% - 40px);
  background-color: #feedd9;
  padding: 10px 20px;
  justify-content: space-between;
}

.statusCards {
  display: flex;
  gap: 20px;
  width: calc(100% - 80px);
  padding: 40px;
  justify-content: space-evenly;
}

.statusCard {
  background-color: #ffffff;
  color: #1f1f2e;
  padding: 20px;
  border-radius: 10px;
  width: calc(100% / 5 - 40px);
  text-align: left;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  font-weight: bold;
  display: flex;
  gap: 1rem;
}
.statusAction {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.statusActive {
  color: #a89798;
}
.statusActionText {
  font-size: 1.2rem;
}
.StatusContent {
  text-align: left;
}
.StatusHeading {
  color: #718ebf;
  font-weight: 400;
  font-size: 0.8rem;
}
.StatusValues {
  color: #2d2d2d;
  font-weight: 700;
  font-size: 1.1rem;
}
.StatusGrowth {
  position: relative;
  top: 50px;
}
