/* Header.css */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  border-bottom: 2px solid #eceff4;
  border-left: 2px solid #eceff4;

  background-color: #ffffff;
}
.header h1 {
  color: #343c6a;
}
.search-bar input {
  outline: none;
  border: none;
  background-color: transparent;
}


.search-bar {
  display: flex;
  align-items: center;
  padding: 10px;
  width: 250px;
  border-radius: 10px;
  border: 1px solid #ddd;
  background-color: #f5f7fa;
  outline: none;
  gap: 10px;
}

.profile img {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  border: 2px solid #ddd;
}
.heading-profile {
  display: flex;
  align-items: center;
  gap: 1rem;
}