.toast {
  position: relative;
  padding: 16px;
  max-width: 450px;
  width: 400px;
  margin-bottom: 10px;
  background-color: white;
  border-radius: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  opacity: 0;
  transform: translateX(100%);
  /* animation: slide-in 1s forwards, fade-out 1s forwards; */
}
.toast-header-close {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.fade-in {
  animation: slide-in 1s forwards;
}
.fade-out {
  animation: fade-out 1s forwards;
}

.toast-content {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.toast-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

.toast-title {
  font-weight: bold;
}

.toast-message {
  font-size: 14px;
  text-align: left;
  margin-bottom: 20px;
  margin-left: 2.5rem;
}

.toast-button {
  background-color: #ff9800;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  margin-left: 2.5rem;
  align-self: start;
}
.toast-close {
  height: 30px;
  width: 30px;
  background-color: #00000059;
  border-radius: 100px;
  border: none;
}

.toast button {
  cursor: pointer;
}

@keyframes slide-in {
  from {
    opacity: 0;
    transform: translateX(100%);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fade-out {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    transform: translateX(100%);
  }
}

.progress-bar {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 4px;
  background-color: green;
  width: 100%;
  animation-name: progress;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
  border-radius: 20px;
}

@keyframes progress {
  from {
    width: 100%;
  }
  to {
    width: 0;
  }
}
@media screen and (max-width: 768px) {
  .toast {
    margin-left: auto;
    margin-right: auto;
  }

  .toast-message,
  .toast-button {
    margin-left: 0; /* Remove extra left margin on smaller screens */
  }

  .toast-header {
    align-items: flex-start;
    gap: 5px;
  }
}

@media screen and (max-width: 480px) {
  .toast {
    max-width: 100%; /* Full width for very small screens */
    width: 90%;
    padding: 12px;
  }

  .toast-title {
    font-size: 1rem; /* Reduce title font size */
  }

  .toast-message {
    font-size: 12px; /* Reduce message font size */
  }

  .toast-button {
    padding: 6px 12px; /* Reduce button size */
    font-size: 0.9rem; /* Smaller font */
  }

  .toast-close {
    height: 25px; /* Reduce close button size */
    width: 25px;
  }

}
@media (max-width: 768px) {
  .toast{
    width: 100%;
    margin: 0 auto;
  }

}
.toast-icon{
  display: flex;
}