.orders-table {
  width: 100%;
  margin-top: 20px;
  border-collapse: collapse;
  border-bottom: 10px solid transparent;
}
.inProgressTable {
  width: 100%;
  margin-top: 20px;
  border-collapse: collapse;
  border-bottom: 10px solid transparent;
}
.orders-table th,
.orders-table td {
  padding: 10px;
  text-align: left;
  border-bottom: 1px solid #ddd;
  background-color: white;
}
.inProgressTable th,
.inProgressTable td {
  padding: 10px;
  text-align: left;
  border-bottom: 1px solid #ddd;
  background-color: white;
}
.inProgressTable th {
  font-size: 14px;
}
.orders-table th {
  font-size: 14px;
}
.preparing {
  background-color: #feedd9;
  color: #f8951d;
  padding: 6px;
  border-radius: 12px;
  font-size: 14px;
  text-align: center;
}
.ready {
  background-color: #aef5d3;
  color: #139456;
  padding: 6px;
  border-radius: 12px;
  font-size: 14px;
  text-align: center;
}
.delay {
  background-color: #e7daff;
  color: #8746ff;
  padding: 6px;
  border-radius: 12px;
  font-size: 14px;
  text-align: center;
}
.cacelled {
  background-color: #fddbdb;
  color: #f42829;
  padding: 6px;
  border-radius: 12px;
  font-size: 14px;
  text-align: center;
}
.orders-table th:nth-child(2),
.orders-table td:nth-child(2) {
  width: 150px;
}
.orders-table p {
  width: 50%;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.popup {
  position: absolute;
  background-color: white;
  padding: 16px 9px 6px 9px;
  z-index: 100;
  width: 192px;
  height: auto;
  border-radius: 10px;
  border: 1px solid  #E5E5E5;
  box-shadow: 7px 6px 30px 0px #09090933;
  right: 0;
}

.popup button {
  display: block;
  width: 100%;
  padding-bottom: 10px;
  border: none;
  background: none;
  text-align: left;
  cursor: pointer;
  font-size: 14px;
  font-weight: 400;
  line-height: 16.8px;
  color: #404040;
  letter-spacing: 0.6px;
  font-family: "Nunito Sans", sans-serif;
  
}

.popup button:hover {
 color: #ffa500;
}
