/* Modal styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background: rgba(0, 0, 0, 0.6);
  overflow: hidden;
  height: 100vh;
  
}

.modal-content {
  background: transparent;
  padding: 20px;
  border-radius: 10px;
}
 


.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.login-box {
  background-color: #ffffff;
  padding: 40px 60px;
  border-radius: 10px;
  text-align: center;
  max-width: 400px;
  width: 400px;
}

.logo {
  width: 132px;
  height: 50px;
}

h1 {
  color: black;
  font-size: 2rem;
  margin: 20px 0;
}

.welcome-text {
  color: #888;
  margin-bottom: 20px;
}

.login-box .input-group {
  margin-bottom: 20px;
  text-align: left;
}

.login-box .input-group label {
  display: block;
  margin-bottom: 5px;
  color: #f8951d;
}

.login-box .input-group input {
  width: -webkit-fill-available;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 1rem;
}

.login-box .password-container {
  position: relative;
}

.toggle-password {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  cursor: pointer;
  color: #888;
}

.options {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.remember-me {
  display: flex;
  align-items: center;
  color: #333;
  font-weight: 500;
}

.remember-me input {
  margin-right: 10px;
}

.forgot-password {
  color: #ff4d4d;
  text-decoration: none;
}

.login-button {
  background-color: #ff8800;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  width: 100%;
}

.sign-up {
  margin-top: 20px;
  color: #333;
}

.sign-up a {
  color: #ff8800;
  text-decoration: none;
}
