.category {
  display: flex;
  padding: 0 20px;
  justify-content: space-between;
  align-items: center;
}
.categorylist {
  display: flex;
  gap: 30px;
  align-items: center;
}
.categories {
  cursor: pointer;
}
.categoriesactive {
  width: 60px;
  height: 40px;
  border-radius: 40px;
  background-color: #f8951d;
  color: white;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}
.categoriesactive p {
  margin: 0;
}
.addDish button {
  background-color: #f8951d;
  color: white;
  border: none;
  width: 100px;
  height: 40px;
  font-size: 16px;
  border-radius: 10px;
  padding: 10px;
  cursor: pointer;
}
.menulist-table {
  padding: 0 20px;
}
