@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;400&display=swap");

.Resturant-header {
  height: 17rem;
  background-color: #374159;
  display: flex;
  align-items: center;
}
.image-grid {
  width: max-content;
  display: grid;
  grid-template-columns: repeat(2, 80px);
  grid-template-rows: repeat(2, 80px);
}
.img-grid {
  width: 80px;
  height: 80px;
  object-fit: cover;
}
.content-header {
  display: flex;
  gap: 2rem;
  padding: 20px;
  /* padding-top: 40px; */
}
.content-text-header {
  display: flex;
  flex-direction: column;
}
.ratting-header {
  color: white;
  /* border-right: 6px white; */
}
.cards {
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
  gap: 4.8rem;
  margin-top: 2rem;

  /* background-color: #fff; */
  /* z-index: 3; */
}
.card-1 {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 7px;
  box-shadow: 0px 4px 100px 0px rgba(0, 0, 0, 0.25);
  /* background-color: black; */
  height: 12rem;
  width: 17rem;
  z-index: 3;
}
.textpara {
  position: relative;
  top: 4rem;
  font-size: 13px;
  font-weight: 500;
  right: 3.75rem;
  font-family: "Poppins", sans-serif;
}
.textpara2 {
  position: relative;
  top: 3rem;
  font-size: 15px;
  font-family: "Poppins", sans-serif;
}
.textpara3 {
  position: relative;
  top: 3rem;
  font-size: 15px;
  font-family: "Poppins", sans-serif;
}
.closed {
  position: relative;
  height: 4rem;
  top: -2rem;
  left: 6.5rem;
}
.text {
  position: relative;
  left: 3rem;
  top: 1.5rem;
  font-weight: 700;
  font-family: "Poppins", sans-serif;
}
.card-2 {
  border-radius: 7px;
  box-shadow: 0px 4px 100px 0px rgba(0, 0, 0, 0.25);
  /* background-color: black; */
  height: 12rem;
  width: 17rem;
  z-index: 3;
}
.menu {
  position: relative;
  height: 4rem;
  top: 2rem;
  left: 1.6rem;
}
.text-2 {
  position: relative;
  right: 2rem;
  top: 3.7rem;
  font-weight: 700;
  font-family: "Poppins", sans-serif;
}
.card-3 {
  border-radius: 7px;
  box-shadow: 0px 4px 100px 0px rgba(0, 0, 0, 0.25);
  /* background-color: black; */
  height: 12rem;
  width: 17rem;
  z-index: 3;
}
.cart {
  position: relative;
  height: 4rem;
  top: 2rem;
  left: 1.5rem;
}
.text-3 {
  position: relative;
  right: 2rem;
  top: 3.7rem;
  font-weight: 700;
  font-family: "Poppins", sans-serif;
}
@media (max-width: 768px) {
  .image-grid {
    grid-template-columns: repeat(2, 70px);
    grid-template-rows: repeat(2, 70px);
    /* gap: 8px; */
  }

  .img-grid {
    width: 70px;
    height: 70px;
  }

  .content-header {
    /* flex-direction: column; */
    align-items: center;
  }

  .cards {
    gap: 1.6rem;
  }

  .card-1,
  .card-2,
  .card-3 {
    height: 10rem;
    width: 14rem;
  }
  .Resturant-header {
    height: 15rem;
  }
}

@media (max-width: 480px) {
  .image-grid {
    grid-template-columns: repeat(2, 50px);
    grid-template-rows: repeat(2, 50px);
    /* gap: 6px; */
  }

  .img-grid {
    width: 50px;
    height: 50px;
  }
  .header-title {
    font-size: 0.7rem;
  }
  .ratting-header {
    font-size: 0.7rem;
  }

  .content-header {
    /* padding: 10px; */
    gap: 1rem;
  }

  .cards {
    gap: 1rem;
  }

  .card-1,
  .card-2,
  .card-3 {
    height: 8rem;
    width: 12rem;
  }

  .textpara {
    top: 3rem;
    font-size: 11px;
  }

  .textpara2,
  .textpara3 {
    top: 2rem;
    font-size: 13px;
  }

  .closed {
    top: -1rem;
    left: 5rem;
  }

  .text,
  .text-2,
  .text-3 {
    top: 1rem;
    font-size: 14px;
  }
  .Resturant-header {
    height: 10rem;
  }
}
@media (max-width: 250px) {
  .image-grid {
    grid-template-columns: repeat(2, 40px);
    grid-template-rows: repeat(2, 40px);
    /* gap: 4px; */
  }

  .img-grid {
    width: 40px;
    height: 40px;
  }

  .content-header {
    /* padding: 10px; */
    gap: 1rem;
  }

  .cards {
    gap: 1rem;
  }

  .card-1,
  .card-2,
  .card-3 {
    height: 6rem;
    width: 10rem;
  }

  .textpara {
    top: 2rem;
    font-size: 10px;
  }

  .textpara2,
  .textpara3 {
    top: 1rem;
    font-size: 11px;
  }

  .closed {
    top: -1rem;
    left: 4rem;
  }

  .text,
  .text-2,
  .text-3 {
    top: 0.5rem;
    font-size: 12px;
  }
  .Resturant-header {
    height: 8rem;
  }
}
