@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;400&display=swap");

.Takeaway-header {
  height: 7rem;
  margin-top: -2rem;
  background-color: #374159;
}
.order {
  width: fit-content;
  color: white;
  position: relative;
  top: 2.5rem;
  display: flex;
  left: 8rem;
  font-size: 29px;
  font-weight: 400;
  font-family: "Poppins", sans-serif;
}
.topnav {
  overflow: hidden;
  background-color: #f1f1f1;
}
.topnav a {
  float: left;
  display: block;
  color: black;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
  border-bottom: 3px solid transparent;
  cursor: pointer;
}
/* .topnav a:hover {
  border-bottom: 6px solid red;
} */
.topnav a.active {
  border-bottom: 6px solid red;
}
