.main-class-card-takeaway {
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 1rem;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.55);
}
.card_header_takeaway {
  display: flex;
  justify-content: space-between;
  padding-bottom: 3px;
  padding-left: 1rem;
  padding-right: 1rem;
}
h3 {
  padding: 0;
  margin: 0;
}
.card_content_takeaway {
  display: flex;
  padding: 0 1rem;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  border-bottom: 3px solid #e0e0e0;
  margin-bottom: 1rem;
}
.card_footer_takeaway {
  display: flex;
  justify-content: space-around;
  padding: 0 1rem;
}
.card_footer_takeaway a {
  text-decoration: none;
  /* color: #374159; */
  font-weight: 600;
  font-size: 1.2rem;
}
.divider-card-takeaway {
  border: 1.5px solid #e0e0e0;
  /* margin: 0 1rem; */
}
.btn_takeaway_accept {
  color: #de6f6f;
}
.btn_takeaway {
  color: #374159;
}
.member-time {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.icon-people {
  width: 1rem;
  height: 1rem;
  margin-right: 0.5rem;
}
.scrollable-container::-webkit-scrollbar {
  width: 10px;
}

.scrollable-container::-webkit-scrollbar-thumb {
  background: #888; /* Color of the scrollbar */
  border-radius: 5px; /* Roundness of the scrollbar */
}

/* Handle on hover */
.scrollable-container::-webkit-scrollbar-thumb:hover {
  background: #555; /* Color of the scrollbar on hover */
}

/* Track of the scrollbar */
.scrollable-container::-webkit-scrollbar-track {
  background: #f1f1f1; /* Color of the scrollbar track */
  border-radius: 5px; /* Roundness of the track (optional) */
}
