.modal-confirm-header h6 {
  margin: 0;
  text-align: left;
  color: #797979;
}
.content-details-dishes {
  max-height: 500px;
  overflow: auto;
}
.customeDetails-orderType {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}
.orderType {
  align-self: flex-end;
  font-size: 14px;
  padding: 10px;
  background-color: #feedd9;
  border-radius: 12px;
}
.orderType p {
  margin: 0;
  color: #f8951d;
}
.Single-Items {
  background-color: #f6f6f6;
  padding: 0 10px;
  text-align: left;
  border-radius: 10px;
  margin-bottom: 10px;
}
.Single-Items p {
  margin: 0;
}
.Single-Items small {
  color: #797979;
}
.Action-button {
  display: flex;
}
.Action-button button {
  width: 100%;
  border-radius: 10px;
  cursor: pointer;
  padding: 10px;
}
.StaffName {
  margin-bottom: 20px;
}
.checkbox-markunavaliable {
  display: flex;
  margin-bottom: 5px;
}
.checkbox-markunavaliable label {
  margin: 0;
  margin-left: 5px;
  color: #737373;
  font-size: 12px;
}
.hline {
  border-bottom: 1px solid #bebebe;
  margin: 10px 0;
}
.Reasons {
  text-align: left;
  margin-bottom: 10px;
}
.Reasons h4 {
  margin: 0;
  margin-bottom: 10px;
  color: #797979;
}
.setTimeEstimation {
  display: flex;
  border: 1px solid #e5e5e5;
  justify-content: space-between;
  margin-bottom: 10px;
}

.setTimeEstimation button {
  border: none;
  background-color: transparent;
  cursor: pointer;
  font-size: 24px;
}
