@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@400;600;700&display=swap');

.verification-popup {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 400px;
    background-color: #fff;
    border-radius: 20px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.5);
    padding: 20px;
    text-align: center;
    width: 608px;
    height: auto;
    font-family: 'Nunito Sans', sans-serif;
  }
  
  /* Logo styling */
  .popup-content .logo {
    margin: auto;
  }
  .popup-content .logo img {
    width: 132px;
    height: 50PX;
    
  }
  
  /* Heading styling */
 .popup-content h2 {
    font-size: 32px;
    font-weight: 700;
    color: #000000;
    margin-top: 8px;
    margin-bottom: 0;
    line-height: 38.4px;
}

  /* Paragraph styling */
 .popup-content p {
    font-size: 18px;
    color: #999EA1;
    font-weight: 400;
    line-height: 21.6px;
    margin-top: 8px;
    padding: 0 30px;
  }
  .popup-content h4{
    font-size: 18px;
    line-height: 21.6px;
    margin-top: 5px;
    margin-bottom: 8px;
    font-weight: 600;
  }
  
  /* Button styling */
  .popup-content .explore-button {
    width: 100%;
    height: 47px;
    background-color: #F8951D;
    color: white;
    font-size: 14px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    margin-top: 8px;
  }
  
  
  