/* MenuTable.css */
@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@400;600;700&display=swap');

.menu-table {
  width: 100%;
  margin-top: 20px;
  border-collapse: collapse;
  border-bottom: 10px solid transparent; /* Adds space below the header */
  font-family: "Nunito Sans", sans-serif;
  
  
}

.menu-table th,
.menu-table td {
  padding: 10px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

.avalaibleDish {
  background-color: white;
}

.menu-table th input,
.menu-table td input {
  margin: 0;
}

.mark-unavailable,
.delete,
.more-options {
  margin: 0 5px;
  padding: 7px 12px;
  border: none;
  cursor: pointer;
  border-radius: 3px;
}

.mark-unavailable {
  background-color: #ffa500;
  color: white;
}

.delete {
  background-color: white;
  color: #f42829;
  border: 1px solid #fbb7b8;
}

.more-options {
  background-color: transparent;
  color: #333;
  font-size: 25px;
}
.menu-table th:nth-child(1),
.menu-table td:nth-child(1) {
  width: 5%;
}
.menu-table th:nth-child(3),
.menu-table td:nth-child(3) {
  width: 20%;
}
.menu-table th:nth-child(4),
.menu-table td:nth-child(4) {
  width: 20%;
}
.menu-table th:nth-child(5),
.menu-table td:nth-child(5) {
  width: 30%;
  position: relative;
}

.menu-table td:nth-child(5) {
  text-align: left;
}
.unavalaibleDish {
  background-color: #e7e7ed;
}
.mark-all {
  position: absolute;
  right: 0;
}
.mark-unavailable {
  width: 8rem;
}
.AcceptButton{
  background-color: #ffa500;
  color: white;
  width: 7rem;
  padding: 7px 12px;
  border: none;
  cursor: pointer;
  border-radius: 3px;

}
.popup {
  position: absolute;
  background-color: white;
  padding: 16px 9px 6px 9px;
  z-index: 100;
  width: 192px;
  height: auto;
  border-radius: 10px;
  border: 1px solid  #E5E5E5;
  box-shadow: 7px 6px 30px 0px #09090933;
  right: 0;
}

.popup button {
  display: block;
  width: 100%;
  padding-bottom: 10px;
  border: none;
  background: none;
  text-align: left;
  cursor: pointer;
  font-size: 14px;
  font-weight: 400;
  line-height: 16.8px;
  color: #404040;
  letter-spacing: 0.6px;
  font-family: "Nunito Sans", sans-serif;
  
}

.popup button:hover {
 color: #ffa500;
}

