.Welcome-page h1 {
  color: #000000;
  font-size: 2rem;
  margin: 20px 0;
}
.Welcome-page .create-account {
  color: #797979;
}
.welcome-login-button {
  background-color: #f7931e;
  color: white;
  border: none;
  padding: 10px 20px;
  margin-top: 20px;
  cursor: pointer;
  border-radius: 7px;
  font-size: 16px;
  width: 300px;
}
.create-account {
  display: block;
  margin-top: 20px;
  color: #797979;
  text-decoration: none;
}
